.header-top {
  background: #ECEFF1;
  padding: 25px 0;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 80px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 615px;
    font-size: 24px;
    line-height: 130%;
    color: #3F51B5;
    margin-left: 50px;
  }
}
.header-bottom {
  background: #0AAA54;
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    li {
      position: relative;
      flex-grow: 1;
      a {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        line-height: 130%;
        color: #ffffff;
        padding: 15px 5px;
      }
      a:hover {
        background: #00BD57;
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: calc(100% - 10px);
        background: #ffffff;
        border-radius: 5px;
        opacity: 0.5;
      }
      &:before {
      content: '';
      position: absolute;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%);
      width: 1px;
      height: calc(100% - 10px);
      background: #ffffff;
      border-radius: 5px;
      opacity: 0.5;
    }
    }
  }
}
.burger {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 30px;
  height: 22px;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #000000;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 9px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 10px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1500px) {
  .header-bottom {
    &__list {
      li {
        a {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 1199px) {
  .header-bottom {
    &__list {
      position: relative;
      flex-wrap: wrap;
      justify-content: start;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: calc(100% + 60px);
        height: 1px;
        background: #ffffff;
        opacity: 0.5;
      }
      li {
        a {
          padding: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__text {
      max-width: 425px;
      font-size: 20px;
      margin-left: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .header-bottom {
    &__list {
      li {
        a {
          padding: 15px 5px;
        }
      }
    }
  }
}

@media screen and (max-width: 570px) {
  .header-top {
    &__inner {
      position: relative;
      display: block;
    }
    &__logo {
      width: 50px;
      margin: 0 auto;
    }
    &__text {
      max-width: 380px;
      font-size: 16px;
      margin: 25px 0 0 0;
    }
  }
  .header-bottom {
    position: fixed;
    z-index: 9;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    padding: 100px 15px 50px;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    &__list {
      flex-direction: column;
      flex-wrap: nowrap;
      gap: 20px;
      &:after {
        display: none;
      }
      li {
        &:after,
        &:before {
          display: none;
        }
        a {
          padding: 0;
        }
      }
    }
  }
  .burger {
    display: block;
  }
}