.footer {
  background: #ECEFF1;
}
.footer-top {
  padding: 30px 0;
  &__date {
    font-size: 16px;
    line-height: 130%;
    color: #707070;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 15px;
    margin-top: 50px;
  }
  &__item {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 130%;
    font-weight: 600;
    color: #000;
  }
  &__text {
    font-size: 16px;
    line-height: 130%;
    color: #000;
  }
  &__phone,
  &__email {
    display: flex;
    align-items: center;
    i {
      max-width: 22px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 5px;
    }
    i svg {
      max-width: 100%;
    }
    p {
      font-size: 16px;
      line-height: 130%;
      color: #000;
    }
    p a {
      font-weight: 700;
    }
    a:hover {
      color: #04AA54;
    }
  }
}
.footer-bottom {
  border-top: 1px solid #D9D9D9;
  padding: 30px 0;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 14px;
      line-height: 130%;
      color: #707070;
    }
    p:nth-child(1) {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 970px) {
  .footer-top {
    &__date {
      font-size: 14px;
    }
    &__items {
      margin-top: 30px;
    }
    &__phone,
    &__email {
      p {
        font-size: 14px;
      }
    }
    &__subtitle {
      font-size: 14px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .footer-bottom {
    &__text {
      p {
        font-size: 14px !important;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__items {
      flex-direction: column;
    }
    &__item:nth-child(1) {
      order: -2;
    }
    &__item:nth-child(2) {
      order: -1;
    }
  }
}