.docs {
  padding: 25px 0 50px;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  .pagination {
    margin-top: 30px;
  }
}
.doc-card {
  border: 1px solid #E6E6E6;
  padding: 30px;
  &:hover {
    border: 1px solid #B6DAC6;
    box-shadow: 0px 0px 10px 0px rgba(0, 41, 19, 0.30);
    .doc-card__text {
      color: #005F2C;
      text-decoration: underline;
    }
    .doc-card__pdf span {
      color: #1E1E1E;
      text-decoration: underline;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 130%;
    color: #1E1E1E;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    gap: 10px 20px;
    margin-top: 30px;
  }
  &__pdf {
    display: flex;
    align-items: flex-end;
    i {
      flex-shrink: 0;
      width: 32px;
      margin-right: 12px;
    }
    span {
      font-size: 14px;
      line-height: 130%;
      color: #ABABAB;
    }
  }
  &__date {
    font-size: 14px;
    line-height: 130%;
    color: #ABABAB;
    span {
      color: #005F2C;
      margin-left: 5px;
    }
  }
}
.pagination {
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
    font-size: 14px;
    line-height: 100%;
    color: #ABABAB;
    &:hover {
      border: 1px solid #0AAA54;
      color: #0AAA54;
    }
  }
  &__prev,
  &__next {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
    &:hover {
      border: 1px solid #0AAA54;
      svg path {
        fill: #0AAA54;
      }
    }
  }
  .points {
    width: 20px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 100%;
    color: rgba(0, 0, 0, 0.25);
    span {
      margin-top: -8px;
    }
    &:hover {
      color: #0AAA54;
    }
  }
  .active {
    background: #04AA54;
    color: #ffffff;
  }
}
.partners {
  padding: 50px 0;
  &__slider {
    position: relative;
    padding: 0 70px;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-next {
    z-index: 1;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
    margin-top: 0;
    &:after {
      display: none;
    }
    &:hover {
      border: 1px solid #04AA54;
      svg path {
        fill: #04AA54;
      }
    }
  }
  .swiper-button-prev {
    z-index: 1;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
    margin-top: 0;
    &:after {
      display: none;
    }
    &:hover {
      border: 1px solid #04AA54;
      svg path {
        fill: #04AA54;
      }
    }
  }
}
.partner-slide {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  img {
    max-width: 100%;
    max-height: 100%;
    margin: 0 auto auto;
  }
  span {
    font-size: 16px;
    line-height: 130%;
    font-weight: 600;
    color: #1E1E1E;
    margin-top: 8px;
  }
}
.breadcrumbs {
  padding: 15px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }
  &__item {
    position: relative;
    font-size: 14px;
    line-height: 130%;
    color: #ABABAB;
    &:hover {
      color: #005F2C;
    }
    &--active {
      color: #005F2C;
    }
    &:not(:last-child) {
      &:after {
        content: '/';
        display: inline-block;
        margin-left: 10px;
      }
    }
  }
}
.search {
  padding: 15px 0 50px;
  .display2 {
    color: #005F2C;
  }
  &-form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
  }
  &-form__group {
    position: relative;
    display: flex;
    align-items: center;
    .input-title {
      max-width: 120px;
      width: 100%;
      flex-shrink: 0;
      font-size: 16px;
      line-height: 130%;
      color: #1E1E1E;
      margin-right: 16px;
    }
    input {
      max-width: 610px;
      width: 100%;
      flex-grow: 1;
      height: 45px;
      border: 1px solid #DDD;
      border-radius: 8px;
      font-size: 16px;
      line-height: 130%;
      color: #1E1E1E;
      padding: 0 12px;
      &::placeholder {
        color: #BBB;
      }
    }
    #date {
      background-image: url("../img/date-icon.svg");
      background-repeat: no-repeat;
      background-position: left 12px center;
      background-size: auto;
      padding-left: 40px;
    }
  }
  .select__wrap {
    max-width: 610px;
    width: 100%;
    padding-bottom: 5px;
  }
  .select__wrap--active {
    .select__list {
      transform: translateY(0);
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__list-default {
    flex-grow: 1;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #DDD;
    border-radius: 8px;
    padding: 0 12px;
    cursor: pointer;
  }
  .select__current {
    font-size: 16px;
    line-height: 130%;
    color: #BBB;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  .select__current--active {
    color: #1E1E1E;
  }
  .select-arrow {
    flex-shrink: 0;
    width: 12px;
    margin-left: 10px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  .select__list {
    position: absolute;
    z-index: 1;
    top: 100%;
    left: 136px;
    transform: translateY(-30px);
    background: #FFF;
    border: 1px solid #DDD;
    border-radius: 8px;
    padding: 15px 5px 15px 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    p {
      font-size: 14px;
      line-height: 130%;
      color: #4D4D4D;
      padding: 5px 20px;
      cursor: pointer;
      &:hover {
        background: rgba(4, 170, 84, 0.20);
      }
    }
    .active {
      background: rgba(4, 170, 84, 0.20);
    }
  }
  .select__list-wrap {
    height: 340px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background: #D9D9D9;
      border-radius: 5px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-left: 136px;
  }
  &__btn {
    height: 45px;
    width: 130px;
    background: #0AAA54;
    border: 1px solid transparent;
    border-radius: 3px;
    span {
      font-size: 16px;
      line-height: 130%;
      color: #FFF;
    }
    &:hover {
      background: transparent;
      border-color: #0AAA54;
      span {
        color: #005F2C;
      }
    }
  }
  &__btn2 {
    width: 155px;
    height: 45px;
    background: transparent;
    border: 1px solid #0AAA54;
    border-radius: 3px;
    span {
      font-size: 16px;
      line-height: 130%;
      color: #005F2C;
    }
    &:hover {
      background: #0AAA54;
      span {
        color: #ffffff;
      }
    }
  }
  &-results {
    margin-top: 100px;
  }
  .docs__cards {
    margin-top: 30px;
  }
}
.air-datepicker-global-container {
  .air-datepicker-nav {
    svg path {
      stroke: #000;
    }
  }
  .air-datepicker-nav--title {
    font-size: 14px;
    line-height: 130%;
    font-weight: 700;
    color: #000;
    i {
      color: #000;
    }
  }
  .air-datepicker-body--day-name {
    font-size: 14px;
    line-height: 130%;
    font-weight: 700;
    text-transform: unset;
    color: #000;
  }
  .air-datepicker-cell {
    font-size: 13px;
    line-height: 130%;
    font-weight: 600;
    color: #333;
  }
  .air-datepicker-cell.-selected- {
    background: #0AAA54;
    color: #ffffff;
  }
  .air-datepicker-cell.-in-range- {
    background: rgba(4, 170, 84, 0.15);
    border-radius: 0;
    color: #0AAA54;
  }
}


@media screen and (max-width: 992px) {
  .partners {
    &__slider {
      padding: 0 50px;
    }
  }
}

@media screen and (max-width: 767px) {
  .partner-slide {
    span {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 576px) {
  .doc-card {
    &__text {
      font-size: 14px;
    }
    &__group {
      margin-top: 15px;
    }
  }
  .pagination {
    &__item:nth-child(5),
    &__item:nth-child(6),
    &__item:nth-child(7),
    &__item:nth-child(8) {
      display: none;
    }
  }
  .search {
    &-results {
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 570px) {
  .partners {
    &__slider {
      padding: 0;
    }
    &__swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
  .partner-slide {
    &:nth-child(4) ~ .partner-slide {
      display: none;
    }
  }
  .search {
    &-form__group {
      display: block;
      .input-title {
        max-width: 100%;
        display: block;
        margin: 0 0 10px 0;
      }
    }
    .select__list {
      left: 0;
    }
    &__btns {
      margin-left: 0;
    }
  }
}
